window.onload = function(){
	// var burgers = document.querySelectorAll('#burger');
	// for (var i = 0; i < burgers.length; i++) {
	// 	burgers[i].onclick = dropMenu;
	// }

	burger1.onclick = dropMenu;
	burger2.onclick = dropMenu;

	function dropMenu(){
		document.querySelector(this.hash).classList.toggle('show');
		if(this.id == 'burger2'){
			document.querySelector('.nav_menu_carousel .wrap').classList.toggle('show');
		}
		return false;
	}

	$('.section_slider .slider').owlCarousel({
		items: 1,
		// nav: true,
		// navText: ['<--', '-->']
	});
	$('.widget .slider').owlCarousel({
		items: 1,
		loop: true
	});
	$('.section_brand .slider').owlCarousel({
		items: 5,
		loop: true,
		nav: true,
		navText: ['<i class="fa fa-chevron-left" aria-hidden="true"></i>', '<i class="fa fa-chevron-right" aria-hidden="true"></i>']
	});
};